import React, { useRef, useState, useEffect, useCallback } from "react";

const Slider: React.FC = () => {
  const min = -15;
  const max = 15;
  const range = max - min;

  const trackRef = useRef<HTMLDivElement>(null);
  const [value, setValue] = useState(0);

  const getValueFromPosition = useCallback(
    (clientY: number) => {
      const track = trackRef.current;
      if (!track) return 0;
      const { top, height } = track.getBoundingClientRect();
      let ratio = (clientY - top) / height;
      ratio = Math.max(0, Math.min(1, ratio));
      const raw = max - ratio * range;
      return Math.round(raw);
    },
    [max, range]
  );

  const handlePointerMove = useCallback(
    (e: MouseEvent | TouchEvent) => {
      e.preventDefault();
      const clientY = "touches" in e ? e.touches[0].clientY : e.clientY;
      setValue(getValueFromPosition(clientY));
    },
    [getValueFromPosition]
  );

  const stopDrag = useCallback(() => {
    document.removeEventListener("mousemove", handlePointerMove as any);
    document.removeEventListener("touchmove", handlePointerMove as any);
    document.removeEventListener("mouseup", stopDrag);
    document.removeEventListener("touchend", stopDrag);
  }, [handlePointerMove]);

  const startDrag = useCallback(
    (e: React.MouseEvent | React.TouchEvent) => {
      e.preventDefault();
      document.addEventListener("mousemove", handlePointerMove as any);
      document.addEventListener("touchmove", handlePointerMove as any);
      document.addEventListener("mouseup", stopDrag);
      document.addEventListener("touchend", stopDrag);
      // initial position update
      const clientY =
        "touches" in e
          ? (e as React.TouchEvent).touches[0].clientY
          : (e as React.MouseEvent).clientY;
      setValue(getValueFromPosition(clientY));
    },
    [getValueFromPosition, handlePointerMove, stopDrag]
  );

  useEffect(() => {
    return () => {
      stopDrag();
    };
  }, [stopDrag]);

  const pointerPosPercent = ((max - value) / range) * 100;

  return (
    <div
      className="relative flex items-center select-none"
      style={{ height: "80vw", width: "4rem" }}
    >
      {/* Track */}
      <div
        ref={trackRef}
        className="absolute left-1/2 top-0 -translate-x-1/2"
        style={{
          width: "4px",
          height: "100%",
          background:
            "linear-gradient(to bottom, blue 0%, blue 50%, red 50%, red 100%)",
        }}
      />

      {/* Step labels */}
      {Array.from({ length: range + 1 }, (_, i) => max - i).map((num) => {
        const pos = ((max - num) / range) * 100;
        return (
          <span
            key={num}
            className={`absolute ${
              num >= 0 ? "text-blue-500" : "text-red-500"
            }`}
            style={{
              top: `${pos}%`,
              left: "2rem",
              transform: "translateY(-50%)",
            }}
          >
            {num}
          </span>
        );
      })}

      {/* Draggable Pointer */}
      <div
        onMouseDown={startDrag}
        onTouchStart={startDrag}
        style={{
          position: "absolute",
          top: `${pointerPosPercent}%`,
          left: "50%",
          transform: "translate(-50%, -50%)",
          width: "1.5rem",
          height: "1.5rem",
          borderRadius: "50%",
          background: "#444",
          cursor: "grab",
        }}
      />
    </div>
  );
};

export default Slider;
